.form {
    display: flex;
    align-items: center;
    margin: 0 auto;
    padding: 15px;
    max-width: 600px;
  }
  
  .buttonForm {
    display: block;
    padding: 12px 20px;
    border-radius: 8px;
    cursor: pointer;
    background-color: #d7fa3c;
    font-size: 20px;
    font-weight: 700;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transform: scale(1);
  }
  
  .buttonForm:hover,
  .buttonForm:focus {
    background-color: blue;
    color: white;
    transform: scale(1.1);
  }
  
  .input {
    display: inline-block;
    width: 400px;
    font: inherit;
    font-size: 20px;
    border: 2px solid black;
    outline: none;
    padding: 10px 8px;
    border-radius: 10px;
    margin-right: 10px;
  }
  
  .input:focus {
    border: 2px solid #d7fa3c;
  }
  
  .iconSearch {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
  }
  