.wrapList {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
  
    max-width: 1600px;
    margin: 0 auto;
    padding: 15px 20px;
  }
  
  .item {
    color: red;
  }
  
  .img {
    transform: scale(1);
    transition: 350ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .img:hover,
  .img:focus {
    transform: scale(1.05);
    box-shadow: 0px 5px 24px 17px rgba(21, 35, 45, 0.29);
  }  